<template>
   <div class="user_card clearfix">
  	 	<div class="item pic">
  	 		<div class="userimg"><img :src="img"></div>
  	 		<div class="username">{{card.nickName}}</div>
  	 	</div>
  	 	<div class="item info">
  	 		<div class="tel">电话：{{card.phone}}</div>
  	 		<div class="signature">个性签名：</div>
  	 		<p class="signature_txt">{{card.sign}}</p>
  	 	</div>
  	 	<div class="item code">
  	 		<div class="codeimg"><img src="../../static/images/code.png"></div>
  	 	</div>
  	 </div>
</template>

<script>
export default {
	data(){
		return {
			card:this.$store.state.user
		}
	},
	computed:{
		img(){
			return this.card.himg || "https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1523692796&di=6f2584abae193c60d25fcc4895274d5f&src=http://img.zcool.cn/community/01786557e4a6fa0000018c1bf080ca.png"
		}
	}
}
</script>

<style>

</style>



